import React from 'react';
import Navbar from './../../components/navbar';
import Footer from './../../components/footer';
// import $ from 'jquery';
// require('foundation-sites');




class Stories extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            page: "Stories",
            width: "0",
            height: "0",
            selectValue: "/contents/Stories/S Tch Tru Cau.htm"
        }
        this.onClick = this.getReceipt.bind(this);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        // $(document).foundation();
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }
      
    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    getReceipt(event) {
        const {title} = event.target;
        if(this.mediaFrame) {
            if(this.state.width >= 1024) {
                this.setState({selectValue: "/contents/" + this.state.page + "/" + title})
            } else {
                var win = window.open("/contents/" + this.state.page + "/" + title, '_blank');
                win.focus();
            }
            
        }
    }

    render() {
        return (
            <div> 
                <Navbar></Navbar>
                <div className="page menu_pdf">
                    <table className="stack" width="100%">
                        <tbody>
                            <tr>
                                <td className="event_menu">
                                    <div className="Main_Panel1" style={{overflow: "auto"}}>
                                        <ul>
                                            <li>
                                                <span title='An Nam Ch Lc.pdf' onClick={this.onClick}>An Nam Ch Lc</span>
                                            </li>
                                            <li>
                                                <span title='Khm nh Vit S Thng Gim Cng Mc.pdf' onClick={this.onClick}>Khm nh Vit S Thng Gim Cng Mc</span>
                                            </li>
                                            <li>
                                                <span title='Lam Sn Thc Lc.pdf' onClick={this.onClick}>Lam Sn Thc Lc</span>
                                            </li>
                                            <li>
                                                <span title='Lch S Vit Nam 01.htm' onClick={this.onClick}>Lch S Vit Nam 01</span>
                                            </li>
                                            <li>
                                                <span title='Quc Triu Chnh Bin Tot Yu.pdf' onClick={this.onClick}>Quc Triu Chnh Bin Tot Yu</span>
                                            </li>
                                            <li>
                                                <span title='S Tch Qu Da Hu.htm' onClick={this.onClick}>S Tch Qu Da Hu</span>
                                            </li>
                                            <li>
                                                <span title='S Tch Tru Cau.htm' onClick={this.onClick}>S Tch Tru Cau</span>
                                            </li>
                                            <li>
                                                <span title='Thch Sanh L Thng.htm' onClick={this.onClick}>An Nam Ch Lc</span>
                                            </li>
                                            <li>
                                                <span title='Truyn Kiu.pdf' onClick={this.onClick}>Truyn Kiu</span>
                                            </li>
                                            <li>
                                                <span title='Trm Trng Trm Con.htm' onClick={this.onClick}>Trm Trng Trm Con</span>
                                            </li>
                                            <li>
                                                <span title='Vit Nam S Lc.pdf' onClick={this.onClick}>Vit Nam S Lc</span>
                                            </li>
                                            <li>
                                                <span title='Vit S Tiu n.pdf' onClick={this.onClick}>Vit S Tiu n</span>
                                            </li>
                                            <li>
                                                <span title='Vit S Ton Th.pdf' onClick={this.onClick}>Vit S Ton Th</span>
                                            </li>
                                            <li>
                                                <span title='i Vit S K Ton Th.pdf' onClick={this.onClick}>i Vit S K Ton Th</span>
                                            </li>
                                            <li>
                                                <span title='i Vit S Lc.pdf' onClick={this.onClick}>i Vit S Lc</span>
                                            </li>
                                            <li>
                                                <span title='i Vit Thng S.pdf' onClick={this.onClick}>i Vit Thng S</span>
                                            </li>
                                        </ul>
                                    </div>
                                </td>
                                <td className="event_frame show-for-large">
                                    <div className="responsive-embed">
                                        <iframe frameBorder="0" className="Truyen" title="PDF Viewer" ref={(iframe) => { this.mediaFrame = iframe;}} src={this.state.selectValue}></iframe>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <Footer></Footer>
            </div>
        );
    }
}

export default Stories;


