import React from 'react';
import Navbar from './../../components/navbar';
import Footer from './../../components/footer';
// const context = require.context("./../../contents/Classrooms/Lop4", true, /\.html$/);
// const context = require('./../../contents/Classrooms/Lop4/Lop4.html');
// const context = require.context('./../../contents/Classrooms/Lop4', true, /\.(pdf)?$/);
// const context = require.context('./../../contents/Classrooms/Lop4', true, /.*/);
// import $ from 'jquery';
// require('foundation-sites');




class Lop4 extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            page: "Classrooms/Lop4",
            width: "0",
            height: "0",
            selectValue: "/contents/Classrooms/Lop4/Lop4.html"
        }
        this.onClick = this.getReceipt.bind(this);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        // $(document).foundation();
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
        // var pdfObj = {};
        // context.keys().forEach(function (key) {
        //     pdfObj[key] = context(key);
        // });
        // console.log(pdfObj);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }
      
    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    getReceipt(event) {
        const {title} = event.target;
        if(this.mediaFrame) {
            if(this.state.width >= 1024) {
                this.setState({selectValue: "/contents/" + this.state.page + "/" + title})
            } else {
                var win = window.open("/contents/" + this.state.page + "/" + title, '_blank');
                win.focus();
            }
            
        }
    }

    render() {
        return (
            <div> 
                <Navbar></Navbar>
                <div className="page menu_pdf">
                    <table width="100%" className="stack">
                        <tbody>
                            <tr>
                                <td className="event_menu">
                                    <div className="Main_Panel1" style={{overflow: "auto"}}>
                                        <ul>
                                            <li>
                                                <span title='2014_B Ti.pdf' onClick={this.onClick}>2014_B Ti</span>
                                            </li>
                                            <li>
                                                <span title='2014_Con Ch.pdf' onClick={this.onClick}>2014_Con Ch</span>
                                            </li>
                                            <li>
                                                <span title='2014_Em Ti.pdf' onClick={this.onClick}>2014_Em Ti</span>
                                            </li>
                                            <li>
                                                <span title='2014_M Ti.pdf' onClick={this.onClick}>2014_M Ti</span>
                                            </li>
                                            <li>
                                                <span title='2015_Anh Chi Em.pdf' onClick={this.onClick}>2015_Anh Chi Em</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ba Em.pdf' onClick={this.onClick}>2015_Ba Em</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ban Em 1.pdf' onClick={this.onClick}>2015_Ban Em 1</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ban Em 2.pdf' onClick={this.onClick}>2015_Ban Em 2</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ban Em.pdf' onClick={this.onClick}>2015_Ban Em</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ky Nghi He.pdf' onClick={this.onClick}>2015_Ky Nghi He</span>
                                            </li>
                                            <li>
                                                <span title='2015_Ma Em.pdf' onClick={this.onClick}>2015_Ma Em</span>
                                            </li>
                                            <li>
                                                <span title='2015_Noi Ve Em.pdf' onClick={this.onClick}>2015_Noi Ve Em</span>
                                            </li>
                                            <li>
                                                <span title='Ba Em.pdf' onClick={this.onClick}>Ba Em</span>
                                            </li>
                                            <li>
                                                <span title='Bn Ti.pdf' onClick={this.onClick}>Bn Ti</span>
                                            </li>
                                            <li>
                                                <span title='Con Ch 13-1-2013.pdf' onClick={this.onClick}>Con Ch 13-1-2013</span>
                                            </li>
                                            <li>
                                                <span title='Con Ch.pdf' onClick={this.onClick}>Con Ch</span>
                                            </li>
                                            <li>
                                                <span title='M Em.pdf' onClick={this.onClick}>M Em</span>
                                            </li>
                                            <li>
                                                <span title='Nh Em 24-2-2013.pdf' onClick={this.onClick}>Nh Em 24-2-2013</span>
                                            </li>
                                            <li>
                                                <span title='Nh Em.pdf' onClick={this.onClick}>Nh Em</span>
                                            </li>
                                            <li>
                                                <span title='Th - T V Em.pdf' onClick={this.onClick}>Th - T V Em</span>
                                            </li>
                                            <li>
                                                <span title='Vn - Ni V Em.pdf' onClick={this.onClick}>Vn - Ni V Em</span>
                                            </li>
                                            <li>
                                                <span title='Vn - vit t do.pdf' onClick={this.onClick}>Vn - vit t do</span>
                                            </li>
                                            <li>
                                                <span title='i Ngh H 27-1-2013.pdf' onClick={this.onClick}>i Ngh H 27-1-2013</span>
                                            </li>
                                        </ul>
                                    </div>
                                </td>
                                <td className="event_frame show-for-large">
                                    <div className="responsive-embed">
                                        <iframe frameBorder="0" title="PDF Viewer" className="Truyen" ref={(iframe) => { this.mediaFrame = iframe;}} src={this.state.selectValue} ></iframe>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <Footer></Footer>
            </div>
        );
    }
}

export default Lop4;


